@charset "UTF-8";
@import "../../assets/styles/mixin";

#Home {
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	position: relative;
	background-color: #e2e2e2;

	.bgc-tips {
		text-align: center;
		color: #fff;
		position: absolute;
		left: 0;
		right: 0;
		top: 80px;
		font-size: 1.2em;
		font-weight: bold;
		text-shadow: 0 3px 1px #8e8e8e;
	}

	.container {
		transform: translateY(0);
	}

	.section {
		width: 100vw;
		height: 100vh;

		&.banner {
			// @include linearCarveUp(rgb(6, 150, 222), rgb(0, 184, 97));
			padding: 100px 0;
			background-image: url('../../assets/images/home/bg-2.jpeg');
			background-size: 100%;
			background-size: cover;
			background-repeat: no-repeat;
			background-position: 60% 50%;

			h2,
			h3 {
				color: #fff;
				text-align: center;
				margin: 10px 0;
				font-weight: 300;
			}

			h2 {
				font-size: 3em;
			}

			h3 {
				font-size: 2em;
			}
		}
	}
	// 
	.k-switch {
		position: fixed;
		right: 2px;
		top: 50%;
		list-style:: none;
		padding: 0;
		margin-top: -27px;
		z-index: 100;

		.k-switch-item {
			width: 12px;
			height: 12px;
			border-radius: 50px;
			background-color: #b5b5b5;
			margin: 10px;
			cursor: pointer;

			&.active {
				background-color: #14c1b1;
			}
		}
	}

	.feij-advantage {
		text-align: center;
		margin-top: 200px;

		.advantage-item {
			display: inline-block;
			margin: 0 30px;

			dd,
			dl,
			dt {
				margin: 0;
				padding: 0;
			}

			dd {
				margin-top: 10px;
				color: #fff;
			}

			.icon-contain {
				background-color: rgba(255, 255, 255, 0.8);
				border-radius: 50%;
				display: inline-block;
				width: 130px;
				height: 130px;
				line-height: 130px;
				text-align: center;
				overflow: hidden;

				.icon {
					display: inline-block;
					background-repeat: no-repeat;
					background-size: 100%;
					background-size: cover;
					vertical-align: middle;
				}

				&.contain-speed-up {
					.icon {
						width: 100px;
						height: 100px;
						background-image: url("../../assets/images/home/speed-up.png");
					}
				}

				&.contain-secure {
					animation-delay: 0.5s;

					.icon {
						width: 76px;
						height: 76px;
						background-image: url("../../assets/images/home/secure.png");
					}
				}

				&.contain-compatible {
					animation-delay: 1s;

					.icon {
						width: 70px;
						height: 82px;
						background-image: url("../../assets/images/home/compatible.png");
					}
				}
			}
		}
	}

	.downloads {
		background-color: #f2f2f2;
		padding: 20px 0 100px;
		text-align: center;

		h3 {
			font-size: 3em;
			text-align: center;
		}

		.download {
			display: inline-block;
			width: 350px;
			margin: 0 15px;
			background-color: #fff;
			border-radius: 6px;
			box-shadow: 2px 2px 8px #b9b9b9;
			padding: 15px 30px 30px;
			vertical-align: top;

			h4 {
				font-size: 1.5em;
			}

			.desc {
				line-height: 1.5;
				height: 150px;
			}

			.btn-download {
				border: none;
				border-radius: 20px;
				height: 40px;
				width: 160px;
				background-color: rgb(6,150,222);
				color: #fff;
				box-shadow: 1px 1px 3px rgb(6,150,222);
			}
		}
	}

	.footer {
		background-color: rgb(40,60,80);
		padding: 5px 20px;

		.logo {
			width: 30px;
			height: 30px;
			vertical-align: middle;
			margin-right: 15px;
		}

		.link {
			color: #bbb;
			font-size: 12px;
			text-decoration: none;
			float: right;
			line-height: 30px;

			&:hover {
				color: #fff;
			}
		}
	}
}
@media screen and (max-width: 768px) {
	#Home {
		.downloads {
			h3 {
				font-size: 2em;
			}

			.download {
				display: block;
				margin-bottom: 15px;
				width: initial;

				.desc {
					height: initial;
					min-height: 80px;
					font-size: 16px;
					margin-bottom: 15px;
				}
			}
		}

		.feij-advantage {
			margin-top: 100px;
			width: 0;
			height: 100px;
			position: relative;
			margin: 150px auto 0;

			.advantage-item {
				display: block;
				margin: 0;
				position: absolute;

				.icon-contain {
					width: 80px;
					height: 80px;
					line-height: 80px;

					&.contain-speed-up {
						.icon {
							width: 60px;
							height: 60px;
						}
					}

					&.contain-secure {
						.icon {
							width: 48px;
							height: 48px;
						}
					}

					&.contain-compatible {
						.icon {
							width: 43px;
							height: 50px;
						}
					}
				}

				&:nth-child(1) {
					top: -100px;
					left: -40px;
				}

				&:nth-child(2) {
					top: 40px;
					left: -150px;
				}

				&:nth-child(3) {
					top: 40px;
					right: -150px;
				}
			}
		}
	}
}



