#dashboard {
	.dashboard-contain {
		min-height: 300px;
		background-color: #fff;
		margin: 0 20px;
		border-radius: 2px;
		box-shadow: 0 0 5px rgba(0, 0, 0, 0.35);
		padding: 20px;
		position: relative;

		.pure-g {
			> div {
				margin-bottom: 20px;
			}
		}

		.controls {
			position: absolute;
			right: 15px;
			top: 18px;

			.btn-link {
				display: inline-block;
				padding: 4px 8px;
				border: 1px solid #ededed;
				text-decoration: none;
				color: #529e42;
				font-size: 12px;
				border-radius: 2px;
				cursor: pointer;
			}
		}
	}

	.pk-modal-container {
		.oops {
			font-size: 12px;
			text-align: left;
			margin: 5px;
			color: #529e42;
		}

		.tips {
			margin-bottom: 10px;
		}

		.pure-control-group {
			> label {
				width: 5em;
			}

			> input {
				display: inline-block !important;
				color: #444;
				border-radius: 4px;
				vertical-align: sub;
			}
		}

		&.VPN {
			.pure-form {
				margin-top: 20px;
			}
		}
	}
}

@media only screen and (max-width: 520px) {
	#dashboard {
		.dashboard-contain {
			.controls {
				position: initial;
				text-align: center;
				margin-bottom: 10px;

				.btn-link {
					margin: 5px;
				}
			}
		}

		.pk-modal-container {
			.pure-control-group {
				text-align: left;

				> label {
					margin-bottom: 5px;
					text-align: left;
				}

				> input {
					width: 100%;
				}
			}

			.pure-form-aligned {
				.pure-controls {
					margin: 1.5em 0 0;
				}
			}
		}
	}
}



